// src/app/categories/[slug]/page.tsx

export const dynamic = 'force-dynamic';

type Props = {
  params: { slug: string };
};

async function getCategory(slug: string) {
  const res = await fetch(`http://localhost:3001/categories/${slug}`, {
    cache: 'no-store', // optionally prevent caching
  });

  if (!res.ok) return null;
  return res.json();
}

export default async function CategorySlugPage({ params }: Props) {
  const slug = await Promise.resolve(params.slug); // âœ… this line fixes the issue
  const data = await getCategory(slug);

  if (!data) return <h2>Category not found</h2>;

  return (
    <main>
      <h1>{slug.toUpperCase()} News</h1>
      <ul>
        {data.news?.map((item: string, i: number) => (
          <li key={i}>{item}</li>
        ))}
      </ul>
    </main>
  );
}

